@import 'main';
// End Of Import

// Start Header




/*
=====
LODEARS
=====
*/
html, body {
    overflow-y: hidden;
}

.pre-loader {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: #FFF;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
        animation: fade .5s infinite alternate;
    }
}
@keyframes fade {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}


 
.owl-carousel .owl-item img {
    width: auto;
}

/* ------ upper bar start ------ */
.upper-bar {

    ul {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin: 0;
        padding: 30px 0 35px 0;
        li:first-child {
            position: relative;
            &::after {
                font-family: "Font Awesome 5 Free";
                font-weight: 900;
                content: "\f107";
                position: absolute;
                right: -10px;
                top: 3px;
                font-size: 13px;
                color: rgb(199, 199, 199);
                cursor: pointer;
            }
            &:hover, &:focus {
                .sign-in {
                    opacity: 1;
                    visibility: visible;
                    height: 60px;
                    transition: all .5s ease-in-out;
                }
            }
            .sign-in {
                opacity: 0;
                visibility: hidden;
                position: absolute;
                top: 20px;
                left: 0;
                width: 90px;
                height: 0px;
                background-color: rgba(204, 51, 65, .8);
                border-radius: 5px;
                transition: all .5s ease-in-out;
                a {
                    color: #FFF;
                    display: block;
                    padding: 3px 12px;
                    &:hover {
                        padding: 3px 15px;
                    }
                }
            }
        }
        li {
            margin: 0 25px 0 0;
            a {
                font-family: reg;
                font-size: 13px;
                color: rgb(199, 199, 199);
                transition: all .5s ease-in-out;
                &:hover, &:focus {
                    text-decoration: none;
                    color: #959494;
                }
            }
        }
    }
}
/* ------ upper bar end ------ */


/* ------ nav bar start ------ */
.zx-nav {
    .zx-container {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        .logo {
            img {
                margin: 0 0 0 15px;
            }
        }
        .zx-search {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            position: relative;
            width: 650px;
            height: 55px;
            border: 1px solid;
            border-color: rgb(230, 230, 230);
            margin: 0 0 0 48px;
            .bootstrap-select {
                width: 238px;
                height: 100%;
                position: relative;
                &::after {
                    content: "";
                    width: 1px;
                    height: 32px;
                    background-color: rgb(221, 221, 221);
                    position: absolute;
                    right: 0;
                    top: 50%;
                    transform: translateY(-50%);
                      
                }
                .dropdown-toggle {
                    height: 100%;
                    border: none;
                    border-radius: 0;
                    padding: 6px 20px 6px 29px;
                    transition: all .5s ease-in-out;
                    &:hover, &:focus {
                        background-color: #FFF;
                    }
                    .caret {
                        right: 20px;
                    }
                    .filter-option-inner-inner {
                        font-family: bold;
                        font-size: 14px;
                        color: rgb(92, 92, 92);
                        text-transform: uppercase;
                    }
                    .open>.dropdown-menu {
                        border: none;
                        border-radius: 0;
                        box-shadow: none;
                        background-color: #FFF !important;
                    }
                    .open>.dropdown-menu {
                        border: none;
                        border-radius: 0;
                        &:hover, &:focus {
                            + .dropdown-toggle {
                                background-color: #FFF !important;
                            }
                        }
                    }
                }
                .btn.dropdown-toggle.btn-default {
                    background: #FFF;
                }
                .btn-default.active, .btn-default:active, .open>.dropdown-toggle.btn-default {
                    background-color: #FFF !important;
                }
                .dropdown-menu {
                    border: none;
                    border-radius: 0;
                    box-shadow: none;
                    top: 97%;
                    padding: 5px 0 12px 0;
                    &:hover {
                        + .btn-default.active, .btn-default:active, .open>.dropdown-toggle.btn-default {
                            background-color: #FFF;
                        }
                    }
                }
                .dropdown-menu li:first-child {
                    display: none;
                }
                .dropdown-menu li {
                    transition: all .3s ease-in-out;
                    a {
                        transition: all .3s ease-in-out;
                        font-family: s-bold;
                        font-size: 15px;
                        color: rgb(162, 162, 162);
                        background-color: transparent;
                        outline: none;
                        padding: 6px 26px;
                        &:hover, &:focus {
                            color: rgb(92, 92, 92);
                        }
                    }
                    &:hover, &:focus {
                        background-color: transparent;
                        outline: none;
                        color: rgb(92, 92, 92);
                    }
                }
            }
            .bootstrap-select > select.mobile-device:focus + .dropdown-toggle, .bootstrap-select .dropdown-toggle:focus {
                outline: none !important;
            }
            input {
                width: 414px;
                height: 100%;
                border: none;
                padding: 6px 6px 6px 33px;
                outline: none;
                &::placeholder {
                    font-family: reg;
                    font-size: 14px;
                    font-style: italic;
                    color: rgb(194, 194, 194);
                }
            }
            .zx-search-icon {
                width: 44px;
                height: 44px;
                background-color: rgb(202, 202, 202);
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                right: 6px;
                top: 50%;
                transform: translateY(-50%);
                transition: all .5s ease-in-out;
                &:hover, &:focus {
                    background-color: rgba(204, 51, 65, .8);
                }
                i {
                    font-size: 16px;
                    color: rgb(255, 255, 255);
                    
                }
                  
            }
        }
        .zx-on-card {
            margin-left: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            a {
                text-decoration: none;
                &:hover {
                    text-decoration: none;
                }
            }
            .items-num {
                display: none;
            }
            img {
                padding-right: 9px;
            }
            p {
                margin: 7px 0 0 0;
            }
            span {
                font-family: reg;
                font-size: 12px;
                color: rgb(92, 92, 92);
                line-height: 1.2;
            }
        }
    }
}
/* ------ nav bar end ------ */

/* ------ select nav start ------ */
