                            font-family: bold;
                            font-size: 15px;
                            color: rgb(92, 92, 92);
                            display: flex;
                            justify-content: flex-start;
                            align-items: center;
                            text-transform: capitalize;
                            &:hover, &:focus {
                                color: rgb(191, 34, 40);
                            }
                            img {
                                padding: 0 16px 0 31px;    
