                    }
                }
            }
        }
        li {
            margin-right: 33px;
            a {
                font-family: s-bold;
                font-size: 14px;
                color: rgb(92, 92, 92);
                transition: all .3s ease-in-out;
                &:hover {
                    text-decoration: none;
                    color: rgb(191, 34, 40);
