}
  
/* ------ select nav end ------ */

/* ------ header start ------ */
.zx-header {
    .main-back {
        img {
            transition: all .5s ease-in-out;
        }
        &:hover {
            img {
                transform: scale(1.01);
            }
        }
    }
    .owl-carousel {
        img {
            width: 100%;
            margin: auto;
        }
    }
    .owl-carousel .owl-dots {
        position: absolute;
        bottom: 40px;
        left: 50%;
        transform: translateX(-50%);
    }
    .owl-carousel .owl-nav button.owl-next,
    .owl-carousel .owl-nav button.owl-prev,
    .owl-carousel button.owl-dot {
        width: 10px;
        height: 10px;
        background-color: rgba(0, 0, 0, 0.502);
        border-radius: 50%;
        margin: 0 5px;
        outline: none;
    }
    .owl-carousel button.owl-dot.active {
        background-color: #FFF;
    }
}
/* ------ header end ------ */

/* ------ offers start ------ */
.sp-offers {
    margin-top: 34px;
    .ad-offer-img {
        overflow: hidden;
        img {
            transition: all .5s ease-in-out;
        }
        &:hover {
            img {
                transform: scale(1.02);
            }
        }
    }
    .offer-img {
        position: relative;
        overflow: hidden;
        img {
            transition: all .5s ease-in-out;
        }
        &:hover {
            img {
                transform: scale(1.02);
            }
            .offer-lable {
                background-color: rgba(244, 157, 33, 0.902);
            }
        }
        .offer-lable {
            background-color: rgba(0, 0, 0, 0.702);
            width: 100%;
            height: 52px;
            position: absolute;
            bottom: 0;
            left: 0;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            transition: all .5s ease-in-out;
            p {
                margin: 0;
                padding: 0 0 0 30px;
                font-family: bold;
                font-size: 16px;
                color: #ffffff;
                text-transform: uppercase;
                text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
            }
        }
    }
}
/* ------ offers end ------ */


/* ------ products start ------ */

.our-products {
    .zx-header {
        height: 55px;
        background-color: rgb(247, 247, 247);
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-left: 3px solid #f49d21;
        padding: 0 38px 0 36px;
        margin-top: 37px;
        h4 {
            font-family: bold;
            font-size: 16px;
            color: rgb(191, 34, 40);
            text-transform: uppercase;
            line-height: 1.2;
        }
        a {
            font-family: reg;
            font-size: 14px;
            font-style: italic;
            color: rgb(167, 167, 167);
            line-height: 1.2;
            transition: all .3s ease-in-out;
            &:hover {
                text-decoration: none;
                color: #f49d21;
            }
        }
    }
    .products {
        .row {
            margin: 15px 0 0 0;
        }
    }
}

/* ------ products end ------ */

/* ------ card start ------ */
.card {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-basis: auto;
    .card-img {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 164px;
        height: 164px;
        img {
        }
    }
    .product-name {
        margin: 12px 0 1px 0;
        a {
            font-family: s-bold;
            font-size: 14px;
            color: rgb(92, 92, 92);
            &:hover {
                text-decoration: none;
            }
        }
    }
    .price {
        
        .new-price {
            font-family: bold;
            font-size: 14px;
            color: rgb(191, 34, 40);
        }
        .old-price {
            font-family: reg;
            font-size: 14px;
            color: rgb(140, 140, 140);
            text-decoration: line-through;
        }
    }
    .overlay {
        opacity: 0;
        visibility: hidden;
        background-color: rgba(255, 255, 255, .6);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        transition: all .5s ease-in-out;
        .quick-view, .add-on-card {
            position: relative;
            top: 30%;
            width: 40px;
            height: 40px;
            background-color: rgb(191, 34, 40);
            opacity: .1;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 7px 0 0;
            cursor: pointer;
            transition: all .4s ease-in-out;
            &:hover {
                background-color: rgb(244, 157, 33);
            }
        }
        .quick-view {
            left: -12px;
        }
        .add-on-card {
            right: -12px;
            margin: 0;
        }
    }
    &:hover {
        .overlay {
            opacity: 1;
            visibility: visible;
            .quick-view, .add-on-card {
                opacity: 1;
            }
            .quick-view {
                left: 0px;
            }
            .add-on-card {
                right: 0px;
            }
        }
    }
}
.card-padding-first-row {
    padding: 20px 0 42px 0;
}
.card-padding-sec-row {
    padding: 20px 0 20px 0;
}
.border-left {
    border-left: 1px solid #ededed;
}
.border-bottom {
    border-bottom: 1px solid #ededed;
}
/* ------ card end ------ */

