                        }
                    }
                }
                .btn.dropdown-toggle.btn-default {
                    background: #FFF;
                }
                .btn-default.active, .btn-default:active, .open>.dropdown-toggle.btn-default {
                    background-color: #FFF !important;
                }
                .dropdown-menu {
                    border: none;
                    border-radius: 0;
                    box-shadow: none;
                    top: 97%;
                    padding: 5px 0 12px 0;
                    height: 160px !important;
                    &:hover {
                        + .btn-default.active, .btn-default:active, .open>.dropdown-toggle.btn-default {
                            background-color: #FFF;
                        }
                    }
                }
                .dropdown-menu li:first-child {
                    display: none;
                }
                .dropdown-menu li {
                    transition: all .3s ease-in-out;
                    a {
                        transition: all .3s ease-in-out;
                        font-family: s-bold;
                        font-size: 15px;
                        color: rgb(162, 162, 162);
                        background-color: transparent;
                        outline: none;
                        padding: 6px 26px;
                        &:hover, &:focus {
                            color: rgb(92, 92, 92);
                        }
                    }
                    &:hover, &:focus {
                        background-color: transparent;
                        outline: none;
                        color: rgb(92, 92, 92);
                    }
                }
            }
            .bootstrap-select > select.mobile-device:focus + .dropdown-toggle, .bootstrap-select .dropdown-toggle:focus {
                outline: none !important;
            }
            input {
                width: 70%;
                height: 100%;
                border: none;
                padding: 6px 6px 6px 33px;
                outline: none;
                &::placeholder {
                    font-family: reg;
                    font-size: 14px;
                    font-style: italic;
                    color: rgb(194, 194, 194);
                }
            }
            .zx-search-icon {
                width: 44px;
                height: 44px;
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                right: 6px;
                top: 50%;
                transform: translateY(-50%);
                transition: all .5s ease-in-out;
                background-color: rgba(204, 51, 65, .8);
                i {
                    font-size: 16px;
                    color: rgb(255, 255, 255);
                    
                }
                  
            }
        }
    }
    .zx-fixed-menu {
        display: block;
        .overlay {
            display: none;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: rgba(0,0,0, .6);
            z-index: 999;
            .side-menu {
                width: 260px;
                height: 100%;
                padding: 55px 16px 6px;
                background-color: #FFF;
                overflow: auto;
                position: absolute;
                left: -260px;
                top: 0;
                transition: all .5s ease-in-out;
                .close {
                    width: 30px;
                    height: 30px;
                    opacity: 1;
                    position: absolute;
                    top: 12px;
                    right: 16px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    i {
                        display: block;
                        color: #717171;
                        font-weight: 600;
                    }
                }
                ul {
                    padding: 0 6px 0px 6px;
                    li {
                        margin: 8px 0;
                        position: relative;
                        a {
                            font-family: reg;
                            font-size: 15px;
                            color: #717171;
                            text-decoration: none;
                            &:hover, &:focus {
                                text-decoration: none;
                            }
                        }
                    }
                    .slide-down {
                        cursor: pointer;
                        ul {
                            display: none;
                            padding: 0 12px 0px 12px;
                            li {
                                a {
                                    color: #717171;
                                }
                            }
                        }
                        &::after {
                            content: '\f067';
                            font-family: 'Font Awesome 5 Free';
                            font-weight: 900;
                            color: #717171;
                            transition: all .5s;
                            position: absolute;
                            top: 0;
                            right: 0;
                            transform: translateY(0%);
                        }
                    }
                    .slide-down.active {
                        &::after {
                            content: '\f068';
                        }
                    }
                }
            }
            .side-menu.slide {
                left: 0;
            }
        }
    }
    .categories {
        display: none;
    }
    .sp-offers {
        .ad-offer-img, .offer-img {
