                }
            }
            h3 {
                &::before {
                    content: '\f078';
                    font-family: 'Font Awesome 5 Free';
                    font-weight: 900;
                    color: #bf2228;
                    transition: all .5s;
                    position: absolute;
                    top: 50%;
                    right: 0;
                    transform: translateY(-50%);
                }
            }
            h3.active {
